::-webkit-scrollbar { display: none; }


#quick {
    /*background-color: #2e3140;*/
    /*background: #fbfbfe;*/
    min-width: 90%;
    /*min-height: 800px;*/
    /*height : 58vh;*/
    /*height: 85vh; */
    position: relative;
}

.wrapper {
    display: flex;
    padding: 10px;
    height: 100%;
}
.row {
    display: flex;
    /*background-color: whitesmoke;*/
    background-color: #272b38;
    font-family: 'Kopub M';
    font-weight: 100;
    font-size: 16px;
    color: rgb(239, 239, 241);
    letter-spacing: -0.5px;
}
.row:nth-child(even) {
    /*background-color: #eeeeee;*/
    background-color: #191f2c;
}
.row:first-child {
    border-top: 1px solid #80808085;
}

.row > div {
    padding: 9px 10px 6px;
}

.attribute {
    background: transparent !important;
    color: whitesmoke;
}
.cell {
    border-bottom: 1px solid #80808085;
    border-right: 1px solid #80808085;
    /*background: whitesmoke;*/
    background: inherit;
}
.attribute .cell:first-child {
    border-bottom: 1px solid #80808000;
}
.cell:first-child {
    border-left: 1px solid #80808085;
}
.lowest-tail {
    display: flex;
    justify-content: space-between;
    /* padding: 0px 10px; */
    box-sizing: border-box;
    margin-top: 10px;
    color: whitesmoke;
}
.float-message-box {
    position: absolute;
    right : 30px;
    top : 30px;
    text-align: right;
    width: 100%;
}
.floating-modal {
    color: #333;
    background-color: rgb(255,205,82);
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    text-align: right;
    top: 30px;
    display: flex;
    width: fit-content;
    margin-left: auto;
    max-width: 45%;
    right: 30px;
    padding: 10px;
    margin-bottom: 5px;
    animation: fadeout 3s;
    animation-fill-mode: forwards;

}
@keyframes fadeout {
    0% {
        opacity: 1;
    }

    70% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
.--flex {
    display: flex;
}
.--relative {
    position: relative;
}
.--inner-eye {
    float: right;
    position: absolute;
    color: whitesmoke;
    font-size: 20px;
    margin: 7px 0 0 10px;
}
.--space-between {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /*border-top: 1px solid whitesmoke;*/
    padding-top: 0px;
    margin-right: 10px;
    margin-top: 2px;
}
.--space-between > .receive-time {
    padding-left: 1px;
}
.--long {
    width: 140px;
    /*width: 215px;*/
}
.--short {
    width: 90px;
}
.--super-short {
    padding: 0px !important;
    width: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 37px;
}
.--extra-short {
    width: 50px;
}
.--elipsis {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.--num {
    text-align: right;
}
.--plus {
    color: rgb(255 52 95);
    text-shadow: 0 0 0 rgb(255 90 126);
}
.--minus {
    /*color: rgb(87, 116, 246);*/
    /*color: rgb(130 152 255);*/
    color: rgb(11 131 255);
    text-shadow: 0 0 0 rgb(59 156 255);
}
.--shown-true {
    display: flex;
}
.--shown-false {
    height: 2px;
    overflow-y:hidden;
    background: #00e6cb !important;
}
.--simpleInput {
    padding: 7px 10px 5px 40px;
    box-sizing: border-box;
    font-weight: 600;
    font-family: 'nanum';
    font-size: 16px;
}
.--red .cell {
    /*background: #ff8d8d;*/
    background-color: #f949496b;
}
.--pointer {
    cursor: pointer;
}

.icon {
    margin-right: 5px;
}
.--green {
    transition: color .1s , transform 2.0s cubic-bezier(.12,.18,0,.99);
    transform: rotate( 360deg );
    /*color: #49b75d;*/
    color: rgb(255,205,82);
}
.--white {
    transition: color 1.5s;
    color: whitesmoke;
}
.--temp {
    margin-right: 10px;
}
.--black {
    transition: 1.5s color;
    color : #333;
}
.--whitesmoke {
    transition: 1.5s color;
    color : whitesmoke;
}
.--hover-mint:hover {
    background-color: rgb(0 255 255 / 45%);
}

.ff-menu {
    display: flex;
}

.lowest {
    /*width: 100%;*/
}

.popup-button {
    display: flex;
    width: 50px;
    padding-right: 3px;
}

.--unable {
    cursor: not-allowed;

}
.--unable .--simpleInput {
    cursor: not-allowed !important;
    background: hsla(0, 0%, 100%, 0.15) !important;
    color: whitesmoke !important;
    pointer-events:none;
}

.--unable .far {
    color: whitesmoke !important;
}
.--hover-bold {
    font-weight: bolder;
    display: flex;
    justify-content: flex-end;
}

.fa-caret-square-down {
    font-size: 23px;
}

input[id^=autoComplete2] {
    width: 200px !important;
    background-clip:padding-box;
    background-color: hsla(0,0%,100%,.2);
    border-bottom-color:rgba(0, 0, 0, 0.05);
    border-bottom-left-radius:4px;
    border-bottom-right-radius:4px;
    border-bottom-style:solid;
    border-bottom-width:2px;
    border-image-outset:0;
    border-image-repeat:stretch;
    border-image-slice:100%;
    border-image-source:none;
    border-image-width:1;
    border-left-color:rgba(0, 0, 0, 0.05);
    border-left-style:solid;
    border-left-width:2px;
    border-right-color:rgba(0, 0, 0, 0.05);
    border-right-style:solid;
    border-right-width:2px;
    border-top-color:rgba(0, 0, 0, 0.05);
    border-top-left-radius:4px;
    border-top-right-radius:4px;
    border-top-style:solid;
    border-top-width:2px;
    box-sizing:border-box;
    color: whitesmoke;
    cursor:text;
    display:block;
    font-family:'nanum';
    font-size:15px;
    font-stretch:100%;
    font-style:normal;
    font-variant-caps:normal;
    font-variant-east-asian:normal;
    font-variant-ligatures:normal;
    font-variant-numeric:normal;
    font-weight:700;
    height:35px;
    letter-spacing:normal;
    line-height:18px;
    margin-bottom:0px;
    margin-left:0px;
    margin-right:0px;
    margin-top:0px;
    padding-bottom:5px;
    padding-left:40px;
    padding-right:10px;
    padding-top:5px;
    text-align:start;
    text-indent:0px;
    text-rendering:auto;
    text-shadow:none;
    text-size-adjust:100%;
    text-transform:none;
    transition-delay:0s, 0s, 0s;
    transition-duration:0.1s, 0.1s, 0.1s;
    transition-property:box-shadow, border, background-color;
    transition-timing-function:ease-in, ease-in, ease-in;
    width:100%;
    word-spacing:0px;
    writing-mode:horizontal-tb;
    -webkit-rtl-ordering:logical;
    -webkit-border-image:none;
}
#autoComplete2:focus {
    background-color: #242627;
    /*border-color: #01cbcb;*/
    border: 3px solid #01cbcb;
    outline: none;
}

